import { createStore } from 'vuex'

export default createStore({
  state: {
    active: 'home',
    NAV_LINKS: [
      {id: 'home', text: 'Home'},
      {id: 'how-to-use', text: 'How to use'},
      {id: 'about', text: 'About'},
      {id: 'for-business', text: 'For business'},
      {id: 'testimonial', text: 'Testimonial'},
      {id: 'download', text: 'Download'}
    ]
  },
  getters: {
  },
  mutations: {
    scrollToElement(state, id) {
      state.active = id
      let el = document.getElementById(id)
      el.scrollIntoView({behavior: 'smooth'})
    }
  },
  actions: {
  },
  modules: {
  }
})
