<template>
    <header class="row">
        <img src="@/assets/01_Header/logo.svg" class="row_logo"/>
        <ul class="row_navbar" :class="{'_active': opened}" >
            <li
                    v-for="link in $store.state.NAV_LINKS"
                    :key="link.id"
                    @click="goto(link.id)"
                    class="row_navbar__link"
                    :class="{_active: link.id === $store.state.active}"
                    >
                <a>{{ link.text }}</a>
            </li>
            <li class="row_btns">
                <app-btn class="invisible" text="Login"/>
                <app-btn class="light" text="Sign up"/>
            </li>
        </ul>
    </header>
    <div class="burger" @click="openMenu" :class="{'_active': opened}"><span></span></div>
</template>

<script>
    import AppBtn from "@/components/UI-kit/btn/AppBtn";
    export default {
        name: "AppHeader",
        components: {AppBtn},
        data() {
            return {
                opened: false
            }
        },
        watch: {
            opened(newValue) {
                newValue ? document.body.style.overflow = 'hidden' : document.body.style.overflow = 'auto'
            }
        },
        computed: {
          isActive(id) {
              return this.$store.state.active === id
          }
        },
        methods: {
            openMenu() {
                this.opened = !this.opened
            },
            goto(id) {
                this.opened = false
                this.$store.commit('scrollToElement', id)
            }
        }
    }
</script>

<style lang="scss" scoped>
    @use "style";
</style>